<template>
  <div class="introduce">
    <div class="leftSide" @mouseover="moveBar" @mouseout="moveBar">
      <p class="title">写在前面</p>
      <p class="detail">从头开始写这个博客站本来的意图是想体验一下完整的网页平面设计过程
      <br>到最后写完以后发现跟预期的差距很大，只留下了一些辐射风的图片孤零零的挂在上面
      <br>主要记录一下平时学习研究过程中的东西，包括一些论文阅读笔记和工程实践笔记，还会开垦一下web方面的前沿技术方面（也许）
      <br>另外就是一些以前做的设计（作品），以后也许会拾起来再做一些
      <br>本站基本没有做适配所以看起来可能会很难受
      </p>
      <div class="viewMore" @click="redirect('oriArticle')">
        <p>VIEW ARTICLE</p>
        <div class="bar" :style="'marginLeft:'+((barPosition)?'30%':'0')+';width:'+((barPosition)?'10%':'30%')"></div>
      </div>
    </div>
    <a href="javascript:" class="imgPart" @click="redirect('oriArticle')">
      <div class="imgContainer">   
        <img src="../../assets/engineerImg.jpg">   
      </div>
      <p class="title">工程&研究 文章</p>
      <p class="detail">主要是前端方向的一些技术研究和爬的一些坑，以及最近研究的nlp和视觉方向论文</p>
    </a>
    <a href="javascript:" class="imgPart" @click="redirect('gallery')">
      <div class="imgContainer">   
        <img src="../../assets/dailyLife.jpg">   
      </div>
      <p class="title">以前做的设计老图</p>
      <p class="detail">大学时候当社长时候做的一些姑且算设计的作品，海报，杂图，网页设计etc</p>
    </a>
  </div>
</template>
<script>
export default {
  name: 'introduce',
  data () {
    return {
      barPosition: false
    }
  },
  methods: {
    moveBar () {
      this.barPosition = !this.barPosition
    },
    redirect (path) {
      this.$router.push('/' + path)
    }
  }
}
</script>
<style lang="less" scoped>
.introduce{
  width: 100%;
  height: 330px;
  margin: 0 auto;
  padding: 50px 10%;
  box-sizing: border-box;
  display: flex;
  .leftSide{
    flex: 1;
    text-align: left;
    cursor: pointer;
    .title{
      font-size: 20px;
      font-weight: 600;
      line-height: 150%;
      margin-bottom: 30px;
    }
    .detail{
      font-size: 12px;
      margin-top: 10px;
    }
    .viewMore{
      margin-top: 20px;
      font-size: 12px;
      color: #fa8b5f;
      font-weight: 600;
      .bar{
        width: 30%;
        height: 3px;
        margin-top: 10px;
        background: #fa8b5f;
        transition: all 0.3s ease;
      }
    }
  }
  .imgPart{
    width: 25%;
    height: 100%;
    text-align: left;
    padding: 1% 2%;
    cursor: pointer;
    .imgContainer{
      margin: 0 auto;
      width: 100%;
      img{
        width: 100%;
      }
    }
    .title{
      margin: 20px 0;
      font-weight: 600;
    }
    .detail{
      font-size: 12px;
    }
  }
}
</style>





