<template>
  <div class="articlePreview">
    <div class="main" @mouseover="showShadow" @mouseout="showShadow" 
    :style="'box-shadow:rgba(0,0,0,'+((shadow)?'0.3':'0.1')+') 0 0 10px'" @click="redirect('/articlePage?id=' + info.id)">
      <div class="title">
        <div class="leftLine"></div>
        <div class="titleBody">
          <p class="header" :style="'color:'+((shadow)?'#fa8b5f':'#000')">{{info.name}}</p>
          <p class="desc">
            <span v-for="(item, index) in info.tag" :key="index">{{item}}</span>
          </p>
        </div>
      </div>
      <div class="body" style="display: -webkit-box;-webkit-line-clamp: 5;-webkit-box-orient: vertical;" v-html="info.content"></div>
      <div class="date">
        {{info.date}}
      </div>
      <div class="topLine"></div>
    </div>
    <div class="selector" @mouseover="moveButton" @mouseout="moveButton">
      <div class="leftButton" :style="'right:'+((buttonPosition)?'-20px':'0')"></div>
    </div>
    <a class="intro" href="javascript:" @mouseover="moveBar" @mouseout="moveBar" @click="redirect('/oriArticle')">
      <p class='title'>最新原创文章</p>
      <p>Newest origin article</p>
      <div class="viewMore">
        <p>VIEW MORE</p>
        <div class="bar" :style="'marginRight:'+((barPosition)?'30%':'0')+';width:'+((barPosition)?'10%':'30%')"></div>
      </div>
    </a>
  </div>
</template>
<script>
export default {
  name: 'articlePreview',
  data () {
    return {
      barPosition: false,
      buttonPosition: false,
      shadow: false,
      info: {}
    }
  },
  mounted() {
    this.getData()
  },
  methods: {
    getData () {
      this.$axios.get('/getArticleList?page=0&size=1')
      .then ( (res) => {
        this.info = res.data.data[0]
        const reg = /<img.*?(?:>|\/>)/gi // 正则匹配图片并去掉。防止简介里面显示图片
        this.info.content = this.info.content.replace(reg, '')
        this.info.tag = this.info.tag.split(';')
      })
      .catch ( (err) => {
        console.log(err)
      })
    },
    moveBar () {
      this.barPosition = !this.barPosition
    },
    moveButton () {
      this.buttonPosition = !this.buttonPosition
    },
    showShadow () {
      this.shadow = !this.shadow
    },
    redirect (path) {
      this.$router.push(path)
    }
  }
}
</script>
<style lang="less" scoped>
.articlePreview{
  padding: 70px 10%;
  display: flex;
}
.selector{
  width: 10%;
  height: 250px;
  position: relative;
  .leftButton{
    width: 0; 
    height: 0;
    border-width: 25px;
    border-style: solid;
    border-color: transparent  transparent transparent #fa8b5f;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    cursor: pointer;
    transition: all 0.5s ease;
  }
  .leftButton::after{
    content: ' ';
    width: 0; 
    height: 0;
    border-width: 30px;
    border-style: solid;
    border-color: transparent  transparent transparent #fff;
    position: absolute;
    top: 50%;
    left: -34px;
    transform: translateY(-50%);
  }
}
.intro{
  flex: 1;
  height: 250px;
  font-size: 18px;
  font-weight: 600;
  text-align: right;
  cursor: pointer;
  margin-right: 15%;
  .title{
    margin: 10px 0;
  }
  .viewMore{
    margin-top: 20px;
    font-size: 12px;
    color: #fa8b5f;
    font-weight: 600;
    .bar{
      width: 30%;
      height: 3px;
      margin-top: 10px;
      background: #fa8b5f;
      transition: all 0.3s ease;
      float: right;
    }
  }
}
.main{
  width: 50%;
  padding: 20px;
  height: 250px;
  transition: all 0.5s ease;
  box-sizing: border-box;
  position: relative;
  cursor: pointer;
}
.main .title{
  display: flex;
  .leftLine{
    width: 1px;
    height: 70px;
    background: #fa8b5f;
  }
  .titleBody{
    margin-left: 30px;
    text-align: left;
    .header{
      font-size: 20px;
      font-weight: 600;
      margin: 10px 0;
      transition: color 0.3s ease;
    }
    .desc{
      font-size: 12px;
      span{
        color: rgb(250, 139, 95);
        margin-right: 20px;
      }
    }
  }
}
.main .body{
  font-size: 12px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;
  margin-top: 20px;
  padding: 0 10px;
  text-align: left;
  height: 95px;
}
.main .date{
  margin-top: 20px;
  text-align: right;
  font-size: 12px;
  color: #999;
}
.main .topLine{
  height: 1px;
  width: 10px;
  background: #fa8b5f;
  position: absolute;
  top: 20px;
  left: 20px;
}

</style>





