<template>
  <div class="photoPreview">
    <a href="javascript:" class="intro" @mouseover="moveBar" @mouseout="moveBar" @click="redirect('/gallery')">
      <p>设计画廊</p>
      <p>Graphic gallery</p>
      <div class="viewMore">
        <p>VIEW MORE</p>
        <div class="bar" :style="'marginLeft:'+((barPosition)?'30%':'0')+';width:'+((barPosition)?'10%':'30%')"></div>
      </div>
    </a>
    <div class="big" @click="openModal">
      <div class="imgContainer" :style="'background-image: url(' + info.img + ')'"></div>
      <div class="wordContainer">
        <p class="title">{{info.name}}</p>
        <p class="desc">{{info.desc}}</p>
        <p class="bigDate">{{info.date[2]}} / {{info.date[1]}}.<br>{{info.date[0]}}</p>
      </div>
    </div>
    <gallery-modal :galleryInfo="info" v-show="isOpenModal" @closeModal="closeModal"></gallery-modal>
  </div>
</template>
<script>
import galleryModal from '../pages/galleryModal'
export default {
  name: 'photoPreview',
  components: {
    galleryModal
  },
  data () {
    return {
      barPosition: false,
      info: {
        date: ['', '', '']
      },
      isOpenModal: false
    }
  },
  mounted() {
    this.getData()
  },
  methods: {
    openModal () {
      this.isOpenModal = true
    },
    closeModal () {
      this.isOpenModal = false
    },
    moveBar () {
      this.barPosition = !this.barPosition
    },
    getData () {
      this.$axios.get('/getGalleryList?page=0&size=1')
      .then ( (res) => {
        const info = res.data.data[0]
        info.date = info.date.split('/')
        this.info = info
      })
      .catch ( (res) => {
        console.log(res)
      })
    },
    redirect (path) {
      this.$router.push(path)
    }
  }
}
</script>
<style lang="less" scoped>
.photoPreview{
  padding: 0 10%;
  display: flex;
  flex-wrap: wrap;
}
.middle{
  width: 46%;
  margin: 25px 2%;
  height: 21rem;
  box-sizing: border-box;
  display: flex;
  .imgContainer{
    width: 40%;
    height: 100%;
    margin-right: 10%;
    background: url(../../assets/middle.png);
    background-size: cover;
    background-position: center;
  }
  .wordContainer{
    text-align: left;
    width: 50%;
    position: relative;
    .title{
      font-size: 16px;
      font-weight: 600;
    }
    .desc{
      margin-top: 10%;
      font-size: 12px;
    }
    .bigDate{
      position: absolute;
      left: 0;
      bottom: 0;
      font-size: 24px;
      font-weight: 600;
    }
  }
}
.intro{
  width: 23%;
  height: 21rem;
  margin: 25px 1%;
  font-size: 22px;
  text-align: left;
  font-weight: 600;
  cursor: pointer;
  p{
    margin: 20px 0;
  }
  .viewMore{
    margin-top: 30px;
    font-size: 12px;
    color: #fa8b5f;
    font-weight: 600;
    .bar{
      width: 30%;
      height: 3px;
      margin-top: 10px;
      background: #fa8b5f;
      transition: all 0.3s ease;
    }
  }
}
.short{
  width: 23%;
  height: 21rem;
  margin: 25px 1%;
  text-align: left;
  .imgContainer{
    width: 100%;
    height: 40%;
    background: url(../../assets/short.jpg);
    background-size: cover;
    background-position: center;
  }
  .title{
    font-size: 16px;
    font-weight: 600;
    margin-top: 10%;
  }
  .smallDate{
    font-size: 12px;
    color: #999;
    margin: 10px 0;
  }
}
.big{
  width: 71%;
  margin: 25px 2%;
  height: 21rem;
  box-sizing: border-box;
  display: flex;
  cursor: pointer;
  .imgContainer{
    width: 60%;
    height: 100%;
    margin-right: 10%;
    background: url(https://ali.baicizhan.com/readin/images/book_wiki/img_1558520105_109136908);
    background-size: cover;
    background-position: center;
  }
  .wordContainer{
    text-align: left;
    width: 30%;
    position: relative;
    .title{
      font-size: 16px;
      font-weight: 600;
    }
    .desc{
      margin-top: 10%;
      font-size: 12px;
    }
    .bigDate{
      position: absolute;
      left: 0;
      bottom: 0;
      font-size: 24px;
      font-weight: 600;
    }
  }
}
</style>






